.header-btns {
  @include mobile {
    margin-right: 15px;
  }
  @include desktops {
    margin-right: 0;
  }
}

.brand-logo {
  // margin-top: 7px;
}

.dynamic-sticky-bg {
  &.reveal-header {
    background: var(--bg);
  }
  &.dark-mode-texts{
    .menu-block.active .nav-link-item{
      color: $shark !important;
    }
    .menu-social-share
     li{
       a{
         color: var(--color-headings-2);
       }
     }
  }
}

.sticky-bg-white {
  &.reveal-header {
    background: #fff;
    .dark-version-logo {
      display: none !important;
    }
    .light-version-logo {
      display: block !important;
    }

    .menu-sep-left {
      position: relative;
      &::before {
        content: "";
        border-left: rgba($shark, 0.5) !important;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 2px;
        height: 26px;
        z-index: 9;
      }
    }
  }
}

.notification-block {
  position: relative;
  .count {
    position: absolute;
    top: 0;
    right: 0;
    margin-top: -4px;
    margin-right: -5px;
  }
}


body[data-theme="dark"],
.dark-mode-texts {
  .reveal-header {
    .menu-social-share {
      li {
        a {
          color: $white;
          transition: 0.4s;
          &:hover {
            color: $electric-violet-2;
          }
        }
      }
    }
    .nav-link-item{
      color: $shark !important;
      @include desktops{
        color: var(--color-headings-2) !important;
      }
    }

  }
}